.control-box {
  padding: 80px 0;
  width: 100%;
  height: 180px;
  margin-left: auto;
  margin-right: auto;
}
.control-box ul {
  list-style: none;
  position: relative;
}
.control-box ul li {
  position: absolute;
  top: 50px;
  cursor: pointer;
}
.control-box img{
  cursor: pointer;
}
.prev {
  left: 0;
}
.next {
  right: 0;
}
.bigbox {
  overflow: hidden;
  width: 806px;
  height: 180px;
  margin: auto 105px auto;
}
#switcher {
  height: 180px;
  left: 0;
}
.content_1.content_2 {
  width: 100%;
  float: left;
}
.box {
  -webkit-transition:all .7s ease 0s;
  transition:all .7s ease 0s;
  width: 265px;
  height: 180px;
  float: left;
  border-left: 3px white solid;
}
.box:hover {
 opacity:0.5;
}
.shadow {
  width: 265px;
  height: 60px;
  /* opacity: 1; */
  background-color: rgba(0, 0, 0, 0.45);
  filter:progid:DXImageTransform.Microsoft.gradient(startColorstr=#66000000,endColorstr=#66000000);
  margin-top: 120px;
  color: white;
  font-size: 14px;
}
.shadow a {
  display: inline-block;
  width: 225px;
  height: 60px;
  color: white;
  text-decoration: none;
  margin-left: 18px;
  margin-top: 10px;
}
@media screen and (max-width: 1020px){
.control-box{
    height: inherit;
    padding: 3rem 0 6rem 0;
}
.control-box ul {
    list-style: none;
    position: relative;
}
.control-box ul li {
    width: 2.8rem;
    position: absolute;
    top: 5.6rem;
    cursor: pointer;
    z-index: 1;
}
.control-box ul li img{
    width: 100%;
}
.bigbox{
    margin: auto 5.6rem auto;
    width: 19.6rem;
    height: 13.4rem;
}
.bigbox img{
    width: 19.6rem;
    height: 13.4rem;
}
.box {
    /*border-left: 3px white solid;*/
    display: block;
    width: 19.6rem;
    height: 13.4rem;
    margin-right: 0;
    font-size: 0;
}
}
